export const submitSalesAssessment = (token, data) => {


  const rootUrl = getRootUrl().um_url;
  return axios({
    method: 'post',
    url: rootUrl + "/sales/insertSalesAssessment",
    headers: {
      Authorization: `Bearer ${token}`
    },
    data: data
  }).then(response => response.data)
};

