const getBadge = status => {
  switch (status) {
    case 20:
      return 'danger';
    case 22:
      return 'success';
    case 21:
      return 'info';
    default:
      return 'primary'
  }
};

const priorityBadge = status => {
  switch (status) {
    case "High":
      return 'danger';
    case "Medium":
      return 'warning';
    case "Low":
      return 'primary';
    default:
      return 'primary'
  }
};

